.output__line {
  margin: 0;
  padding: 0;
  /* Стилизация */
  background: none;
  border: none;
  /* Параметры текста */
  font-weight: bold;
  font-size: 60px;
  line-height: 1;
  color: inherit;
}

@media screen and (max-width: 750px) {
 .output__line {
  font-size: 40px;
 }
}
